body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgba(255, 255, 255, 0.0);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ShowButton {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 64px;
  height: 64px;
  background-color: transparent;
  border: 0px;
  cursor: pointer;
  padding: 0%;
}

.Phone {
  background-color: whitesmoke;
  border-radius: 3px;
  padding: 0.5rem;
  border: darkslateblue solid 1px;
}

.PhoneHeader {
  background-color: rgb(91, 94, 161);
  color: wheat;
  border-radius: 3px 3px 0 0;
  vertical-align: middle;
  font-size: 16px;
  line-height: 36px;
  text-overflow: ellipsis;
  font-family: Helvetica, Arial, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-stretch: semi-expanded;
}

.WidgetHideButton {
  background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAADAAAAAwAQMAAABtzGvEAAAABlBMVEUAAAD///+l2Z/dAAAAAXRSTlMAQObYZgAAABNJREFUGNNjGJnA/v//P9gpCgEA3aMM5UssVesAAAAASUVORK5CYII=);
  width: 24px;
  height: 24px;
  background-size: 24px;
  float: right;
  margin-top: 10px;
}

.Buttons {
  padding-top: 20px;
}

.Button {
  margin: 10px;
}